.toggle{
    height: 25px;
    width: 50px;
    border-radius:12.5px;
    position: relative;
    background-image: linear-gradient(aqua, skyblue);
    cursor: pointer;
}
.toggle:hover { 
    box-shadow: 0 0 2px rgba(33,33,33,1);
}
.toggle.night {
    background-image: linear-gradient(midnightblue,rebeccapurple);
    box-shadow: 0 0 3px whitesmoke;
}
.notch {
    height:25px;
    width: 25px;
    border-radius: 50%; 
    background: yellow;
    position: absolute;
    box-shadow: 0 0 1.25px yellow;
    transition: all 0.3s ease-in-out;
}
.night > .notch {
    background: whitesmoke;
    box-shadow: 0 0 1.25px whitesmoke;
    -webkit-transform: translate(25px, 0);
            transform: translate(25px, 0);
}

